body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


html, body {margin: 0; height: 100%; overflow: hidden}


.santa-page h2 {
    text-align: right;
    color: red;
    font-size: 4rem;
    margin-right: 15px;
    margin-top: 15px;
}

.top-bar {
    margin-left: auto;
    margin-right: auto;
    height: auto;
    width: 50%;
    text-align: left;
}

.name-list {
    margin: 0 auto;
    margin-top: 25px;
    color: rgba(18, 18, 18, 0.82);
    font-weight: 700;
    height: 45vh;
    width: auto;
    overflow: scroll;
    background-color: rgba(121,136,121,0.66);
    padding: 15px;
}

.name-list * {
    margin-top: 4px;

}

.santa-page {
    background: url(../../static/media/elves.a64260e3.jpg) no-repeat center center fixed;
    background-size: cover;
    height: 100vh;
    overflow: hidden;
    font-size: 1.5rem;
    width: 100vw;
}

.add-name {
    padding: 15px;
    border-radius: 5px;
    background-color: forestgreen;
    margin-left: 5px;
}

.submit-names {
    padding: 15px;
    border-radius: 5px;
    /*margin-left: 50px;*/
    background-color: #61dafb;
}

#submit {
    margin-left: 5px;
}

input {
    height: 15px;
    margin-left: 5px;
    margin-top: 5px;
    outline: none;
    border: none;
    padding: 15px;
    -webkit-appearance: none;
    border-radius: 5px;
}

.results {
    position: relative;
    top: 0;
    height: 100vh;
    width: 100vw;

    background: url(../../static/media/bells.9ea3c948.jpg) no-repeat center center fixed;
    background-size: cover;
    overflow: hidden;
}

.results h2 {
    text-align: right;
    font-size: 4rem;
    margin-right: 15px;
    margin-top: 15px;
    color: green;
}

.hide {
    display: none;
}

#print {
    margin-top: 30px;
    font-size: 2rem;
    border-radius: 5px;
    text-decoration: none;
}
